import cx from 'classnames';
import styles from './side-menu-style';
import { sections } from '../../components';
import { throttle } from '../../utils'

let sectionsPositions = [];

const handleResize = () => {
    const scrollOffset = 200;
    sectionsPositions = [];
    sections.forEach(section => {
        const { offsetTop } = document.getElementById(section.sectionId);
        const headerSectionHeight = document.getElementById('section_hero').clientHeight;
        sectionsPositions.push(offsetTop + headerSectionHeight - scrollOffset);
    });
    sectionsPositions.push(Infinity);
}

const handleScroll = () => {
    const { scrollY } = window;
    const activeSectionIndex = sectionsPositions.findIndex(
        sectionsPosition => sectionsPosition >= scrollY
    );
    const headerSectionHeight = document.getElementById('section_hero').clientHeight;
    const sectionButtons = Array.from(document.getElementsByClassName('SectionButton'));
    if (headerSectionHeight > scrollY) {
        sectionButtons.forEach((sectionButton) => {
            sectionButton.classList.remove(styles.activeSection);
        });
        return;
    }
    if (scrollY + window.innerHeight === document.getElementsByTagName('body')[0].clientHeight) {
        sectionButtons.forEach((sectionButton) => {
            sectionButton.classList.remove(styles.activeSection);
        });
        sectionButtons[sectionButtons.length - 1].classList.add(styles.activeSection);
        return;
    }
    sectionButtons.forEach((sectionButton, sectionButtonsIndex) => {
        if (
            sectionButtonsIndex === activeSectionIndex
        ) {
            sectionButton.classList.add(styles.activeSection);
        } else {
            sectionButton.classList.remove(styles.activeSection);
        }
    });
}

export const SideMenu = () => {
    window.addEventListener('load', throttle(handleResize, 50));
    window.addEventListener('resize', throttle(handleResize, 50));
    document.addEventListener('scroll', throttle(handleScroll, 50));
    return (
        <header className={cx(styles.sideMenu)}>
            <nav>
                {sections.map(({ sectionId, sectionName, sectionIcon }) => (
                    <button
                        key={sectionId}
                        className="SectionButton"
                        id={`SectionButton_${sectionId}`}
                        title={sectionName}
                    >
                        {sectionIcon}
                    </button>
                ))}
            </nav>
        </header>
    )
}
