import styles from './section-header-style';

export const SectionHeader = ({ label, number, children }) => (
    <div className={styles.sectionHeaderWrapper}>
        <div className={styles.sectionHeader}>
            <p>{number}.</p>
            <span>{label}</span>
            <hr />
        </div>
        <div className={styles.sectionContent}>
            {children}
        </div>
    </div>
);