const debounce = (fn, ms = 0) => {
    // https://www.30secondsofcode.org/js/s/debounce
    let timeoutId;
    return function (...args) {
        clearTimeout(timeoutId);
        timeoutId = setTimeout(() => fn.apply(this, args), ms);
    };
};

const throttle = (fn, delay) => {
    // https://stephencharlesweiss.com/javascript-debounce-throttle-function/
    let scheduledId
    return function throttled() {
        const context = this
        const args = arguments
        const throttledCall = fn.apply(context, args)
        if (scheduledId) return
        scheduledId = setTimeout(() => {
            typeof throttledCall === 'function' && throttledCall()
            clearTimeout(scheduledId)
        }, delay)
    }
}

export {
    throttle,
    debounce,
}
