import { useEffect } from 'react';
import { throttle } from '../../utils'

const config = {
    debug: false,
    particleSize: 5,
    particleCount: 10,
    particleSpeed: .05,
    particleLifetime: 500,
    onClickParticleCount: 10,
};

let scrollPositionPercentage = 0;

const initAnimation = () => {
    let fpsLastCall = 0;
    let fps = 0;
    let delta = 0;
    let addingParticleIndex = config.particleCount;
    const particleCache = {};
    const ctx = document.getElementById('background-animation').getContext('2d');
    const updateFps = () => {
        if(!fpsLastCall) {
            fpsLastCall = Date.now();
            fps = 0;
         }
         delta = (Date.now() - fpsLastCall) / 1000;
         fpsLastCall = Date.now();
         fps = Math.floor(1 / delta);
       document.getElementById('fps-counter').innerText = `${fps}`
    }
    const fitCanvas = () => {
        ctx.canvas.width = window.innerWidth;
        ctx.canvas.height = window.innerHeight;
    }
    const randomPositionOnCanvas = () => ({
        x: Math.ceil(Math.random() * ctx.canvas.width),
        y: Math.ceil(Math.random() * ctx.canvas.height),
    });
    const randomGravity = () => ({
        x: randomDirection() ? Math.ceil(Math.random() * 100) / 10 : -(Math.ceil(Math.random() * 100) / 10),
        y: randomDirection() ? Math.ceil(Math.random() * 100) / 10 : -(Math.ceil(Math.random() * 100) / 10),
    });
    const randomDirection = () => (Math.random() - 0.5) * 2 >= 0;
    const randomSpeedMultiplier = () => Math.random() > 0.6 ? Math.floor(Math.random() * 10) + 1 : 1;
    const addUserParticle = (event) => {
        const x = event.clientX;
        const y = event.clientY;
        for(let i = 0; i < config.onClickParticleCount; i++){
            addParticle(addingParticleIndex, {x, y});
            addingParticleIndex = addingParticleIndex + 1;
        }
    }
    const addParticle = (index, startCoordinates = false) => {
        const lifetime = config.particleLifetime * Math.ceil(Math.random() * 100) / 100;
        const position = startCoordinates ? startCoordinates : randomPositionOnCanvas();
        particleCache[index] = {
            ...position,
            originalPosition: position,
            gravity: randomGravity(),
            lifetime,
            customSpeedMultiplier: randomSpeedMultiplier(),
            killable: !!startCoordinates,
            originalLifetime: lifetime,
        };
    }
    const addParticles = (particleCount) => {
        let i = 0;
        for (i = 0; i < particleCount; i++) {
            addParticle(i);
        }
        animationFrame();
    }
    const animationFrame = () => {
        let i = 0;
        ctx.clearRect(0, 0, window.innerWidth, window.innerHeight);
        do {
            if (particleCache[i]) {
                const {
                    x,
                    y,
                    gravity,
                    lifetime,
                    originalLifetime,
                    originalPosition,
                    killable,
                    customSpeedMultiplier,
                } = particleCache[i];
                let newLifetime = lifetime - 1;
                let opacity;
                let trailOpacity = 1;
                let size;
                const lifetimePercentage = Math.ceil(newLifetime / originalLifetime * 100) / 100;
                if (newLifetime < 1) {
                    if (killable) {
                        delete particleCache[i];
                    } else {
                        addParticle(i);
                    }
                } else {
                    let particleSpeed = config.particleSpeed;
                    if (lifetimePercentage > 0.5) {
                        size = config.particleSize - config.particleSize * lifetimePercentage
                        opacity = 1 - newLifetime/originalLifetime;
                    } else {
                        size = config.particleSize * lifetimePercentage
                        opacity = newLifetime/originalLifetime;
                        trailOpacity = opacity * 2;
                    }
                    const xNew = x + gravity.x * particleSpeed * customSpeedMultiplier;
                    const yNew = y + gravity.y * particleSpeed * customSpeedMultiplier;
                    // colors
                    const trailColor = `rgba(55, 77, 117, ${trailOpacity})`;
                    const borderColor = `rgba(255, 255, 255, ${opacity})`;
                    const cometColorTop = `rgba(255, 255, 255, ${(100 - scrollPositionPercentage) / 100})`;
                    const cometColorBottom = `rgba(0, 243, 255, ${scrollPositionPercentage / 100})`;
                    // trailrgb()
                    ctx.beginPath();
                    ctx.setLineDash([5]);
                    ctx.strokeStyle = trailColor;
                    ctx.moveTo(originalPosition.x, originalPosition.y);
                    ctx.lineTo(xNew, yNew);
                    ctx.stroke();
                    // comet border
                    ctx.beginPath();
                    ctx.setLineDash([]);
                    ctx.strokeStyle = borderColor;
                    ctx.arc(xNew, yNew, size, 0, 2 * Math.PI);
                    ctx.stroke();
                    // comet fill and glow
                    ctx.shadowOffsetX = 0;
                    ctx.shadowOffsetY = 0;
                    ctx.shadowBlur = lifetimePercentage * 10;
                    ctx.fillStyle = cometColorTop;
                    ctx.shadowColor = cometColorTop;
                    ctx.fill();
                    ctx.shadowBlur = lifetimePercentage * 30;
                    ctx.fillStyle = cometColorBottom;
                    ctx.shadowColor = cometColorBottom;
                    ctx.fill();
                    ctx.fill();
                    ctx.shadowBlur = 0;
                    particleCache[i] = Object.assign(
                        particleCache[i],
                        { x: xNew, y: yNew, lifetime: newLifetime }
                    );
                }
            }
            i += 1;
          } while (i < addingParticleIndex);
        config.debug && updateFps();
        requestAnimationFrame(animationFrame);
    }
    const startAnimation = () => {
        ctx.clearRect(0, 0, window.innerWidth, window.innerHeight);
        addParticles(config.particleCount);
    }
    const updateScrollPosition = () => {
        if (window.scrollY === 0) {
            scrollPositionPercentage = 0;
            return;
        }
        scrollPositionPercentage = Math.round((window.scrollY / (document.body.clientHeight - window.innerHeight)) * 100);
    }
    const init = () => {
        window.addEventListener('resize', fitCanvas);
        document.addEventListener('scroll', throttle(updateScrollPosition, 50));
        document.addEventListener('mousedown', addUserParticle);
        fitCanvas();
        startAnimation();
    }
    init();
}

export const BackgroundAnimation = () => {
    useEffect(initAnimation, [])
    return (
        <>
            {config.debug && <div
                id="fps-counter"
                style={{
                    position: 'fixed',
                    zIndex: 10,
                    top: 0,
                    right: 0,
                }}
            />}
            <canvas
                id="background-animation"
                style={{
                    position: 'fixed',
                    zIndex: 1,
                    top: 0,
                    left: 0,
                    maxWidth: 'initial',
                }}
            />
        </>
    )
};